:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.light-theme {
  color: #17161a;
  background: linear-gradient(315deg, #eee01f 3%, #589adb 38%, #4da4df 68%, #d2e243 98%) 0 0 / 400% 400% fixed;
  margin: auto;
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  animation: gradient 15s infinite;
  overflow: auto;
}

.dark-theme {
  color: #b6aaff;
  background: linear-gradient(315deg, #270249 3%, #03305e 38%, #45014b 68%, #722a01 98%) 0 0 / 400% 400% fixed;
  margin: auto;
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  animation: gradient 15s infinite;
  overflow: auto;
}

@keyframes gradient {
  0% {
    background-position: 0 0;
  }

  50% {
    background-position: 100% 100%;
  }

  100% {
    background-position: 0 0;
  }
}

.search-form {
  height: 60px;
  background-color: #070349;
  justify-content: center;
  margin-bottom: 60px;
  display: flex;
}

.search-input {
  height: 30px;
  width: 300px;
  border-radius: 5px;
  margin-top: 10px;
}

.search-btn {
  height: 35px;
  cursor: pointer;
  transition: transform var(--animation-duration) var(--timing-function);
  background-color: #f5f5f5;
  border-radius: 5px;
  margin-top: 11px;
  margin-left: 10px;
  font-size: medium;
  box-shadow: 0 2px 5px rgba(0, 0, 0, .3);
}

.checkbox {
  opacity: 0;
  margin: 20px 0 0 20px;
}

.checkbox-wrapper {
  margin-top: 20px;
}

.checkbox + label {
  cursor: pointer;
  color: #837ee9;
  padding: 0 0 0 60px;
  position: relative;
}

.checkbox + label:before {
  content: "";
  width: 50px;
  height: 26px;
  background: #d7dacd;
  border-radius: 13px;
  transition: all .2s;
  position: absolute;
  top: -4px;
  left: 0;
  box-shadow: inset 0 2px 3px rgba(0, 0, 0, .2);
}

.checkbox + label:after {
  content: "";
  width: 22px;
  height: 22px;
  background: #fff;
  border-radius: 10px;
  transition: all .2s;
  position: absolute;
  top: -2px;
  left: 2px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, .3);
}

.checkbox:checked + label:before {
  background: #070349;
  display: flex;
}

.checkbox:checked + label:after {
  left: 26px;
}

.checkbox:focus + label:before {
  box-shadow: inset 0 2px 3px rgba(0, 0, 0, .2), 0 0 0 1px rgba(240, 240, 234, .7);
}

.load-more-btn {
  height: 50px;
  width: 200px;
  cursor: pointer;
  transition: transform var(--animation-duration) var(--timing-function);
  background-color: #f5f5f5;
  border-radius: 5px;
  margin: 100px auto 0;
  font-size: medium;
  display: block;
  box-shadow: 0 2px 5px rgba(0, 0, 0, .3);
}

.load-more-btn.is-hidden {
  cursor: pointer;
  display: none;
}

.load-more-btn:hover, .search-btn:hover {
  transform: scale(1.03);
}

.gallery {
  flex-wrap: wrap;
  gap: 20px;
  list-style: none;
  display: flex;
}

.photo-card {
  transition: transform var(--animation-duration) var(--timing-function);
  flex-basis: calc(25% - 15px);
  margin-bottom: 100px;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.photo-card:hover {
  transform: scale(1.03);
}

.gallery-img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  display: block;
}

.info {
  padding: 10px;
}

b {
  margin-right: 5px;
  font-size: 12px;
  font-weight: 600;
}

